import React from 'react';
import { useAppState } from 'shared/state';
import { useEntityActionPopup, useNotistack, usePagination } from 'shared/hooks';
import EntityContainer from 'modules/entity/containers';
import {
	NewButton,
	Pagination,
	TableBody,
	TableContainer,
	TableHead
} from 'shared/components/Elements';
import Loader from 'shared/components/Loader';
import {
	ActionsButtons,
	ConfirmModal,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import clsx from 'clsx';
import { IconDownload } from 'shared/icons';
import IconButton from '@material-ui/core/IconButton';
import { InvoiceType } from '../../types';
import dayjs from 'dayjs';
import { FaUsers } from 'react-icons/fa';
import { Link } from 'react-router-dom';
import { decrementEntityTotal, formRequest } from '../../../../modules/entity/actions';
import { useDispatch } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { convertToLocalizationKey } from 'shared/services/utils/convertToLocalizationKey';

const formatDate = (date: string) => (date ? dayjs(date).format('DD/MM/YYYY') : 'No Date');

function Invoices() {
	const { t } = useTranslation();
	const { companyId } = useAppState();
	const { page, handlePage } = usePagination();
	const dispatch = useDispatch();
	const { showNotification } = useNotistack();

	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<InvoiceType>();

	const invoiceDeleteRequest = (invoice: InvoiceType) => {
		//show loader
		toggleDeleteLoading();
		dispatch(
			formRequest({
				entity: 'invoices',
				name: 'AllInvoices',
				method: 'delete',
				url: `payment/${companyId}/invoices/${invoice.id}/`,
				deleteData: true,
				id: invoice.id,
				cb: {
					success: () => {
						showNotification({ message: t('successfully_deleted'), variant: 'success' });
						dispatch(decrementEntityTotal('staffEmployee', `staffEmployee-${companyId}`));
					},
					error: (err) => {
						showNotification({ message: err?.response?.data?.message, variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
						closeDeletePopup();
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	};

	const deleteClientHandler = () => () => invoiceDeleteRequest(deleteVisible!);

	return (
		<div>
			{!!companyId && (
				<EntityContainer.All
					entity="invoices"
					name="AllInvoices"
					url={`payment/${companyId}/invoices/`}
					replaceIds
					params={{
						limit: 10,
						extra: {},
						page
					}}
				>
					{({ items, isFetched, meta }) => {
						const invoiceItems = items as InvoiceType[];
						return (
							<>
								{!!invoiceItems.length && (
									<>
										<TableContainer classes={{ root: 'relative ' }}>
											<StyledTable
												aria-label="simple table"
												classes={{ root: 'table-fixed dark-mode-styled-table' }}
											>
												<TableHead>
													<StyledTableRow>
														<StyledTableHeadCell style={{ width: '10%' }}>
															Invoice ID
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '15%' }}>
															{t('billing_start_date')}
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '15%' }}>
															{t('billing_end_date')}
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '10%' }}>
															{t('members')}
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '20%' }}>
															{t('invoice_amount')}
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '15%' }}>
															{t('status')}
														</StyledTableHeadCell>
														<StyledTableHeadCell style={{ width: '15%' }}>
															{t('actions')}
														</StyledTableHeadCell>
													</StyledTableRow>
												</TableHead>
												<TableBody className="font-poppins text-sm">
													{invoiceItems.map((invoiceItem, idx) => {
														const {
															id,
															start_date,
															end_date,
															staffs_count,
															payment_status,
															amount,
															is_paid,
															is_monthly,
															invoice
														} = invoiceItem;
														return (
															<StyledTableRow key={id}>
																<StyledTableCell className="!font-medium">
																	{`#${id}`}
																</StyledTableCell>
																<StyledTableCell>
																	<span>{formatDate(start_date)}</span>
																</StyledTableCell>
																<StyledTableCell>
																	<span>{formatDate(end_date)}</span>
																</StyledTableCell>
																<StyledTableCell>
																	<div className=" flex-center-y">
																		<FaUsers className="mr-2 text-lg opacity-20" />
																		<span className="font-semibold">{staffs_count}</span>
																	</div>
																</StyledTableCell>
																<StyledTableCell>
																	<strong className="font-semibold">{`$${amount}`}</strong>
																</StyledTableCell>
																<StyledTableCell>
																	<strong
																		className={clsx('font-semibold uppercase', {
																			'text-primary-dark': is_paid,
																			'text-kred-pink': !is_paid
																		})}
																	>
																		{t(convertToLocalizationKey(payment_status))}
																	</strong>
																</StyledTableCell>
																<StyledTableCell>
																	<div className="flex-center-y">
																		<NewButton
																			color={!is_paid ? 'primary' : 'default'}
																			disabled={is_paid}
																			className={clsx('relative w-16', { 'opacity-50': is_paid })}
																		>
																			<Link
																				className="absolute inset-0 dark:text-white dark:hover:text-primary"
																				to={`/checkout/${id}/${
																					is_monthly ? '1' : '0'
																				}/${staffs_count}`}
																			>
																				{t('pay')}
																			</Link>
																		</NewButton>
																		<IconButton
																			className="!ml-2 h-12 w-12"
																			disabled={!invoice?.url}
																			classes={{ label: 'block absolute inset-0' }}
																		>
																			<a
																				href={invoice?.url}
																				download
																				className="flex-centered absolute inset-0"
																			>
																				<IconDownload
																					className={clsx('text-xl', {
																						'!text-kblue': !is_paid,
																						'!text-kgrey': is_paid
																					})}
																				/>
																			</a>
																		</IconButton>
																		{invoiceItem.payment_status === 'UNPAID' ? (
																			<ActionsButtons.DeleteButton
																				isDisabled={false}
																				clickHandler={() => showDeleteVisible(invoiceItem)}
																			/>
																		) : null}
																	</div>
																</StyledTableCell>
															</StyledTableRow>
														);
													})}
												</TableBody>
											</StyledTable>
										</TableContainer>
										<ConfirmModal
											featureHandler={deleteClientHandler()}
											isOpen={Boolean(deleteVisible)}
											toggleHandler={closeDeletePopup}
											title={t('delete_invoice_confirmation', { invoiceId: deleteVisible?.id })}
											hasLoader
											isLoading={deleteLoading}
										/>
									</>
								)}
								{!isFetched && <Loader tiled withOverlay />}
								<div className="news__pagination my-4">
									{items.length ? (
										<Pagination
											count={meta?.last_page}
											page={page ? +page : 1}
											defaultPage={6}
											siblingCount={1}
											color={'primary'}
											onChange={handlePage}
										/>
									) : null}
								</div>
							</>
						);
					}}
				</EntityContainer.All>
			)}
		</div>
	);
}

export default Invoices;
